.benefits_list {
  display: none;
}
@media (min-width: 1024px) {
  .benefits_list {
    font-family: "Rubik", sans-serif;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(229, 229, 229);
    border-radius: 0.25rem;
    padding: 16px 0 16px 0;
  }
}
.benefits_list_element img {
  position: center;
  margin-right: 10px;
  height: 30px;
}

.header_box {
  font-family: "Rubik", sans-serif;
  line-height: 62px;
}
.header_box:before, .header_box :after {
  height: 2px;
  width: 100px;
  border: solid 1px rgb(0, 0, 0);
}
.header_box_logo {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px 0 15px 0;
}
.header_box_logo img {
  height: 100px;
  width: 100px;
  margin-right: 20px;
}
.header_box_logo h1 {
  font-size: 50px;
  font-weight: 700;
  text-transform: uppercase;
}
.header_box_menu_list {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(252, 163, 17);
  border-radius: 0.25rem;
  font-size: 20px;
  font-family: "Rubik", sans-serif;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 1px;
}
.header_box_menu_list * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}
.header_box_menu_list li {
  display: inline-block;
  list-style: outside none none;
  margin: 0 1.5em;
}
.header_box_menu_list a, .header_box_menu_list span {
  color: rgb(0, 0, 0);
  position: relative;
  display: inline-block;
}
.header_box_menu_list a:before, .header_box_menu_list span:before {
  position: absolute;
  content: "";
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
  opacity: 0;
  top: 25%;
  bottom: 25%;
  left: 0;
  right: 0;
  border-top: 3px solid #34495e;
  border-bottom: 3px solid #34495e;
}
.header_box_menu_list a:hover, .header_box_menu_list span:hover {
  color: #ffffff;
}
.header_box_menu_list a:hover:before, .header_box_menu_list span:hover:before {
  opacity: 1;
  top: 0;
  bottom: 0;
}
.header_box_menu_list_element {
  margin-left: 20px;
}

.container {
  margin: 0 auto;
  max-width: 1440px;
}

body {
  padding: 0 20px;
}

.cta_box {
  padding: 20px 0 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .cta_box {
    flex-direction: row;
  }
}
.cta_box img {
  width: calc(100vw - 40px);
  margin: 10px 0 20px 0;
  border-radius: 0.25rem;
}
@media (min-width: 768px) {
  .cta_box img {
    width: 50%;
  }
}
.cta_box_text {
  width: calc(100vw - 40px);
  font-family: "Rubik", sans-serif;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .cta_box_text {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .cta_box_text {
    margin-left: 40px;
  }
}
.cta_box_text h2 {
  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
}
@media (min-width: 1024px) {
  .cta_box_text h2 {
    font-size: 32px;
  }
}
.cta_box_text p {
  font-size: 16px;
  margin: 30px 0 50px 0;
}
@media (min-width: 1024px) {
  .cta_box_text p {
    font-size: 20px;
  }
}

.cta_btn {
  align-items: center;
  background-clip: padding-box;
  background-color: rgb(252, 163, 17);
  border: 1px solid transparent;
  border-radius: 0.25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: rgb(255, 255, 255);
  cursor: pointer;
  display: inline-flex;
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0.5px;
  justify-content: center;
  line-height: 1.25;
  margin: 0;
  min-height: 3rem;
  padding: calc(0.875rem - 1px) calc(1.5rem - 1px);
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
}
.cta_btn:hover, .cta_btn :focus {
  background-color: #fb8332;
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
  color: rgb(0, 0, 0);
}
.cta_btn:hover {
  transform: translateY(-1px);
}
.cta_btn:active {
  background-color: #c85000;
  box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
  transform: translateY(0);
}

